@font-face {
    font-family: "CookieRun-Regular";
    src: url("https://fastly.jsdelivr.net/gh/projectnoonnu/noonfonts_2001@1.1/CookieRun-Regular.woff")
        format("woff");
    font-weight: normal;
    font-style: normal;
}

.App {
    font-family: "CookieRun-Regular";
    /* text-align: center; */
}

button {
    font-family: "CookieRun-Regular";
    border: none;
    border-radius: 5px;
    padding: 5px 15px;
    background-color: pink;
}

button:hover {
    cursor: pointer;
}

.sort-btn {
    margin: 10px 5px;
    color: palevioletred;
    background-color: #eee;
}

.insert-btn {
    margin-left: 10px;
}

.delete-btn {
    margin-bottom: 10px;
    background-color: pink;
}

.nav-bar {
    display: flex;
    align-items: center;
    background-color: #282c34;
    color: white;
    width: 100%;
    height: 60px;
    font-size: 20px;
    padding-left: 20px;
}

div {
    box-sizing: border-box;
}

.list {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding-left: 20px;
    border-bottom: 1px solid pink;
}

.title-box {
    display: flex;
    justify-content: center;
    align-items: center;
}

.title {
    padding-right: 15px;
}

.title:hover {
    cursor: pointer;
}

.like {
    padding-right: 8px;
}

.like:hover {
    cursor: pointer;
}

.modal {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 20px;
    margin-top: 20px;
    background-color: #eee;
}
